<script>

import liveTag from '@/components/liveTag'
import memberPrice from '@/components/memberPrice'
import commonCheckbox from '@/components/checkbox/common-checkbox'
import { mapGetters } from 'vuex'
/**
 * list 基础数据
 * switchStyle 切换商品独立排列样式，true=大图样式 false=单条样式
 */
export default {
  props: {
    liveinfo: {
      type: Array,
      default: () => []
    },
    list: {
      type: Object,
      default: () => {},
    },
    switchStyle: {
      type: Boolean,
      default: false
    },
    canCheck: {
      type: Boolean,
      default: false
    },
    visitStore: {
      type: String,
      default: '',
    },
    shareId: {
      type: String,
      default: '',
    },
    taskId: {
      type: String,
      default: '',
    },
    stageId: {
      type: String,
      default: '',
    },
  },
  components: {
    liveTag,
    commonCheckbox,
    memberPrice
  },
  computed: {
    ...mapGetters('mymanage', ['displayCommission']),
    alreadyBuy() {
      return (this.list.signStoreFlag !== null) && (this.list.signStoreFlag !== undefined)
    },
    alreadyBuyText() {
      if (this.visitStore && this.list.signStoreFlag) {
        return '本店买过的商品'
      }
      if (this.visitStore && this.list.signStoreFlag === false) {
        return '买过的商品'
      }
      if (!this.visitStore) {
        return '买过的商品'
      }
      return '';
    },
    nameTitleOneLine() {
      if (this.switchStyle) {
        return false;
      }
      return !(!this.list.productTitle && !this.list.productAct && !this.list.sellPoint);
    }
  },
  data() {
    return {}
  },
  mounted() {
  },
  methods: {
    /**
     * 打开商品详情页面
     * productId id
     */
    fnOpenGoodsDetail() {
      let param = {
        productId: this.list.productId,
      }
      if (this.visitStore) {
        param.visitStore = this.visitStore
      }
      if (this.shareId) {
        param.shareId = this.shareId
      }
      if (this.list.skku) {
        param.skku = this.list.skku
      }
      if (this.list.comId) {
        param.comId = this.list.comId
      }
      if (this.list.caseId) {
        param.caseId = this.list.caseId
      }
      if (this.list.undertakeStore) {
        param.visitStore = this.list.undertakeStore
      }
      if (this.taskId) {
        param.taskId = this.taskId
      }
      if (this.stageId) {
        param.stageId = this.stageId
      }
      if (this.$route.name === 'home') {
        this.$util.gioTrackWithVariable('MB13809', {
          // user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
          // member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
          roughly_classification_id: this.list.productCateId ? this.list.productCateId + '' : 'null',
          productId_var: this.list.productId + '',
          site_number: ~~this.list.listIndex + 1 + ''
        })
      }
      this.$util.gioTrackWithVariable('mall_proList_click_product', {
        productId_var: this.list.productId + ''
      })
      this.$emit('handleGoodClick', this.list)
      this.$router.push({
        path: '/goodsDetail',
        query: param
      })
    },
    sortList(arr) {
      let len = arr.length;
      for (var i = 0; i < len; i++) {
        for (var j = 0; j < len - 1 - i; j++) {
          if (arr[j].tagOrder > arr[j + 1].tagOrder) {
            var temp = arr[j + 1]
            arr[j + 1] = arr[j]
            arr[j] = temp
          }
        }
      }
      return arr
    },
    /**
     * @description 获取字符串中的字节数
    */
    getCharLength(str) {
      let iLength = 0
      for (let i = 0; i < str.length; i++) {
        if (str.charCodeAt(i) > 255) {
          iLength += 2
        } else {
          iLength += 1
        }
      }
      return iLength
    }

  },
}
</script>

