<template>
  <div v-if="productList && productList.length>0" :class="{'goods-list':!switchStyle,'double-list':switchStyle,'collection-list':collection}">
    <template v-if="collection">
      <template v-if="!switchStyle">
      <div :key="item.productId" v-for="item in productList">
        <van-swipe-cell >
          <div class="li">
            <single-and-double-permutation :taskId="taskId" :stageId="stageId" :liveinfo="skulive && skulive[item.productId]" :list="item" :switchStyle="switchStyle" :shareId="shareId" :canCheck="canCheck" @handleGoodClick="handleGoodClick"/>
          </div>
          <template slot="right">
            <van-button square type="danger" text="删除" @click="cancelCollection(item)"/>
          </template>
        </van-swipe-cell>
      </div>
      </template>
       <!-- 瀑布流 -->
      <div v-else class="two-column collection">
        <div class="column">
          <div v-for="item in leftList" :key='item.productId'>
            <single-and-double-permutation :taskId="taskId" :stageId="stageId" :liveinfo="skulive && skulive[item.productId]" :list="item"  :visitStore="visitStore" :switchStyle='switchStyle' :shareId="shareId" :canCheck="canCheck" @handleGoodClick="handleGoodClick"/>
          </div>
        </div>
        <div  class="column">
          <div v-for="item in rightList" :key='item.productId'>
          <single-and-double-permutation :taskId="taskId" :stageId="stageId" :liveinfo="skulive && skulive[item.productId]" :list="item"  :visitStore="visitStore" :switchStyle='switchStyle' :shareId="shareId" :canCheck="canCheck" @handleGoodClick="handleGoodClick"/>
          </div>
        </div>
      </div>
    </template>
    <!-- 单排和大图模式 -->
    <template v-else>
      <div  v-if="!switchStyle">
        <div :key="item.productId" v-for="item in productList" class="li" >
          <single-and-double-permutation :taskId="taskId" :stageId="stageId" :liveinfo="skulive && skulive[item.productId]" :list="item"  :visitStore="visitStore" :shareId="shareId" @handleGoodClick="handleGoodClick"/>
        </div>
      </div>
      <!-- 瀑布流 -->
      <div v-else class="two-column">
        <div class="column">
          <div v-for="(item, index) in leftList" :key='item.productId'>
            <single-and-double-permutation :taskId="taskId" :stageId="stageId" :liveinfo="skulive && skulive[item.productId]" :list="item"  :visitStore="visitStore" :switchStyle='switchStyle' :shareId="shareId" @handleGoodClick="handleGoodClick(item,index+1)"/>
          </div>
        </div>
        <div  class="column">
          <div v-for="(item, index) in rightList" :key='item.productId'>
          <single-and-double-permutation :taskId="taskId" :stageId="stageId" :liveinfo="skulive && skulive[item.productId]" :list="item"  :visitStore="visitStore" :switchStyle='switchStyle' :shareId="shareId" @handleGoodClick="handleGoodClick(item,index+2)"/>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import singleAndDoublePermutation from '@/components/singleAndDoublePermutation/index'
/**
 * productList 基础数据
 * switchStyle 是否是大图模式展示 true=大图 false=单条展示
 * collection  是否是收藏页面进入，涉及到侧滑删除 true=展示 false=不展示
 */
export default {
  props: {
    skulive: {
      type: Object,
      default: () => {}
    },
    productList: {
      type: Array,
      default: () => [],
    },
    switchStyle: {
      type: Boolean,
      default: false,
    },
    collection: {
      type: Boolean,
      default: false,
    },
    canCheck: {
      type: Boolean,
      default: false
    },
    visitStore: {
      type: String,
      default: '',
    },
    shareId: {
      type: String,
      default: '',
    },
    taskId: {
      type: String,
      default: '',
    },
    stageId: {
      type: String,
      default: '',
    },
  },
  components: {
    singleAndDoublePermutation
  },
  data() {
    return {
      tagList: [
        { color: '#f10', plain: false, context: '自营' },
        { color: '#467bf1', plain: false, context: '息期免息' },
        { color: '#f2826a', plain: true, context: '智家优选' },
        { color: '#f2826a', plain: true, context: '智家优选' },
        { color: '#f2826a', plain: true, context: '智家优选' },
        { color: '#f2826a', plain: true, context: '智家优选' },
        { color: '#f2826a', plain: true, context: '智家优选' },
        { color: '#ffe1e1', text: '#ad0000', plain: true, context: '商家免邮' }
      ]
    }
  },
  computed: {
    leftList() {
      if (!this.productList) return []
      return this.productList.filter((v, i) => {
        return i % 2 === 0;
      })
    },
    rightList() {
      if (!this.productList) return []
      return this.productList.filter((v, i) => {
        return i % 2 !== 0;
      })
    }
  },
  watch: {
  },
  methods: {
    /**
     * @description 删除已收藏的商品
     */
    cancelCollection(item) {
      // 回调取消收藏方法
      this.$emit('deleteCollection', item);
    },
    /**
     * @description 商品点击
    */
    handleGoodClick(item, index) {
      item.index = index
      this.$emit('handleGoodClick', item);
    }
  },
}
</script>

<style lang="scss" scoped>
// 单排
.goods-list {
  background-color: #fff;
  padding:0 0.24rem;
  .li {
    min-height: 2.06rem;
    display: flex;
  }
  padding-bottom: 0.24rem;
}
// 大图模式
.double-list {
  width:100%;
  box-sizing:border-box;
  padding: 0 0.24rem;
  display: flex;
  flex-wrap: wrap;
  .li:nth-child(odd){
    div{
      margin-right: 0.14rem;
    }
  }
}
// 大图瀑布流
.two-column{
  display: flex;
  flex-direction: row;
  width: 7.1rem;
  justify-content: space-between;
  .column{
    display: flex;
    flex-direction: column;
  }
}
.two-column.collection{
  width: calc(100% - .24rem);
}
// 收藏模式
.collection-list{
  padding-right: 0;
  ::v-deep .public-single-row-goods{
    min-height: 1.9rem;
    padding-right: 0.24rem;
     .right{
        width: 5.09rem;
        .goods-tag{
          padding-right: 0.24rem;
          height: 0.3rem;
          overflow: hidden;
          ul{
            height: 0.3rem;
            overflow: hidden;
          }
        }
     }
  }
}
</style>
